export const slashChar = (options, input) => {
  const direction = options.direction
  const position = options.position
  const directionCharacter = () => {
    switch (direction) {
      case "forward":
        return "/"
      case "back":
        return '"'
      default:
        return "/"
    }
  }
  return () => {
    switch (position) {
      case "start":
      case "beginning":
      case "front":
        return directionCharacter + input
      case "end":
      case "back":
        return input + directionCharacter
      default:
        return directionCharacter + input
    }
  }
}
